.container {
  position: fixed;
  top: 0;
  width: 260px;
  height: 100vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 0px 8px 0px ;
  border-radius: 0px;
  transition: left 0.3s ease-in-out;
  background-color: var(--color-grey-80);
  box-shadow: -2px -2px 57px -28px rgba(34, 60, 80, 0.46);
}

.tabbar {
  position: fixed;
  top: 0;
  width: 4px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding: 24px;
  border-radius: 0px;
  transition: left 0.5s ease-in-out;
  background-color: var(--color-grey-80);
  box-shadow: -2px -2px 57px -28px rgba(34, 60, 80, 0.46);
}

.header {
  padding-top: 16px;
}

.product-container {
  padding: 12px;
  border: 1px solid var(--color-grey-20);
  box-shadow: none;
}

.products {
  overflow-y: scroll;
  padding-top: 12px;
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.nutrients {
  display: flex;
  flex-wrap: wrap;
  row-gap: 4px;
  margin-bottom:  8px;
  margin-top: 8px;
}
.nutrient {
  display: inline-flex;
  gap: 4px;
  justify-content: center;
  align-items: center;
  min-width: 25%;
}

.close {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.productHeader {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.closeProduct {
  margin-top: -6px;
  cursor: pointer;
}